import React from "react"
import { graphql, StaticQuery } from "gatsby"
import Layout from "../components/Layout"
import DownloadCV from "../assets/svg/downloadCV.svg"
import Programming from "../assets/svg/programming.svg"
import Database from "../assets/svg/database.svg"
import Tools from "../assets/svg/tools.svg"
import portrait from "../../static/images/portrait-small.jpg"

const About = () => (
  <StaticQuery
    query={aboutQuery}
    render={data => (
      <Layout>
        <main id="about">
          <div className="vertical-center">
            <h1 className="page-title">About Me</h1>
            <div className="container">
              <AboutHeader
                aboutHeader={data.site.siteMetadata.aboutHeader}
                pdf={data.pdf}
              />
              <div className="about-info">
                <AboutAside aboutAside={data.site.siteMetadata.aboutAside} />
                <AboutSection
                  aboutSection={data.site.siteMetadata.aboutSection}
                />
              </div>
            </div>
          </div>
        </main>
      </Layout>
    )}
  />
)

const AboutHeader = ({ aboutHeader, pdf }) => {
  return (
    <div className="about-header">
      <figure className="bio">
        <img src={portrait} alt="Yunet Luis RuÃ­z" className="bio-image" />
        <figcaption className="bio-desc">
          <Greeting />
          <p>
            {aboutHeader.desc}
            <a
              id="link-CV"
              href={pdf.publicURL}
              target="_blank"
              rel="noopener noreferrer"
            >
              {verifyIcon(aboutHeader.iconName)}
            </a>
          </p>
        </figcaption>
      </figure>
    </div>
  )
}

const AboutAside = ({ aboutAside }) => (
  <aside className="about-aside">
    <div className="about-div">
      <h3>{aboutAside.title}</h3>
      <ul className="about-list">
        {aboutAside.info.map(item => (
          <li key={item.id}>
            <p>{item.desc}</p>
          </li>
        ))}
      </ul>
    </div>
  </aside>
)

const AboutSection = ({ aboutSection }) => (
  <section className="about-section">
    <div className="about-div">
      <h3>{aboutSection.skills.title}</h3>
      {aboutSection.skills.info.map(item => (
        <div key={item.iconName} className="about-skills">
          <div>
            {verifyIcon(item.iconName)}
            <em>{item.name}</em>
          </div>
          <p>{item.desc}</p>
        </div>
      ))}
    </div>
  </section>
)

const verifyIcon = iconName => {
  switch (iconName) {
    case "Programming":
      return <Programming />
    case "Database":
      return <Database />
    case "Tools":
      return <Tools />
    case "DownloadCV":
      return <DownloadCV />
    default:
      console.log(iconName)
  }
}

const Greeting = () => {
  let hours = new Date().getHours()
  const greeting =
    hours < 12
      ? "Good morning"
      : hours >= 12 && hours < 18
      ? "Good afternoon"
      : "Good evening"
  return <h2 id="greeting">{greeting}</h2>
}

const aboutQuery = graphql`
  query {
    site {
      siteMetadata {
        aboutHeader {
          iconName
          desc
        }
        aboutAside {
          title
          info {
            id
            desc
          }
        }
        aboutSection {
          skills {
            title
            info {
              iconName
              name
              desc
            }
          }
        }
      }
    }
    pdf: file(name: { eq: "CV" }) {
      name
      extension
      publicURL
    }
  }
`

export default About
